import React, {FC, useState, useRef} from "react";
import './styles/styles.scss';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, {
    Thumbs
} from 'swiper';
import 'swiper/swiper.min.css'

SwiperCore.use([Thumbs]);

export type ImageSliderProps = {
    items: string[]
};

export const ImageSlider: FC<ImageSliderProps> = ({items}) => {
    const [thumbsSwiper, setThumbsSwiper] = useState(null);
    const swiperRef = useRef(null);

    return (
        <div className="imageSlider">
              {/*<img src={items[swiperIndex]}/>*/}
              <Swiper
                  spaceBetween={0}
                  slidesPerView={1}
                  className="bigSlider"
                  thumbs={{swiper: thumbsSwiper}}
              >
                  {
                      items.map((item) =>
                          <SwiperSlide >
                              <div className="imageSlider__Item">
                                  <img decoding="async" loading="lazy" src={item}/>
                              </div>
                          </SwiperSlide>
                      )
                  }
              </Swiper>
            <Swiper
                onSwiper={setThumbsSwiper}
                spaceBetween={13}
                slidesPerView={5}
                className="minSlider"
                freeMode={true}
                watchSlidesProgress
                // @ts-ignore
                ref={swiperRef}
            >
                {
                    items.map((item, index) =>
                        <SwiperSlide key={index + item}>
                            <div className="imageSlider__ItemMini">
                                <img decoding="async" loading="lazy" src={item}/>
                            </div>
                        </SwiperSlide>
                    )
                }
            </Swiper>
        </div>
    );
}
