import React, {FC, useEffect, useState} from "react";
import './styles/styles.scss';

//Components
import {BuyButton, CatalogItemProps} from "../../CatalogItem/CatalogItem";

import {CartItemProps} from "../../Cart/components/CartBlock/CartItem/CartItem";
import {priceFormater} from "../../../lib/priceFormater";
import {musicPlayer} from "../../../mock/musicPlayer";
import {useDispatch, useSelector} from "react-redux";
import {AppState} from "../../../../../FaduDrum/store/store";
import {addToCart} from "../../../store/slice/cart";

export type ProductInfoPanelProps = CatalogItemProps & {

};

export const ProductInfoPanel: FC<ProductInfoPanelProps> = ({
    NAME = `Товар`,
    SUBTITLE= `Подзаголовок`,
    PRICE= 0,
    DESCRIPTION = ``,
    TONE = `-`,
    WEIGHT,
                                                                IMAGE,
                                                                ID
                                                            }) => {
    const [inCart, setInCart] = useState<boolean | undefined>(null);
    const {items} = useSelector((state: AppState) => state.cart);
    const dispatch = useDispatch();

    useEffect(() => {
        if (items.find((item) => item.ID === ID)) {
            setInCart(false)
        } else {
            setInCart(true)
        }
    }, [items]);

    const onClickHandler = () => {
        const payload: CartItemProps = {
            ID,
            IMAGE,
            NAME,
            PRICE,
            COUNT: 1
        }

        dispatch(addToCart(payload));
    };
    return (
        <section className="productInfoPanel">
            <div className="productInfoPanel__Content">
                <h3 className="productInfoPanel__Name">
                    {NAME}
                </h3>
                <h3 className="productInfoPanel__Subtitle">
                    {SUBTITLE}
                </h3>
                <div className="productInfoPanel__Price">
                    <span>
                        {priceFormater.format(PRICE) + " ₽"}
                    </span>
                </div>
                <BuyButton inCart={inCart} onClickHandler={onClickHandler} big/>
                <div className="productInfoPanel__Description">
                    <span>
                        {DESCRIPTION}
                    </span>
                </div>
                <div className="productInfoPanel__CharList">
                    <span>
                        Настройка: {TONE}
                    </span>
                        <span>
                        Вес: {WEIGHT}кг
                    </span>
                </div>
                {/*<MusicPlayer {...musicPlayer} />*/}
            </div>
        </section>
    );
}