import React, {FC, useEffect, useState} from "react";
import './styles/styles.scss';
// Components
import {Row, Col} from "antd";
import {ImageSlider} from "../ImageSlider/ImageSlider";
import {ProductInfoPanel} from "./ProductInfoPanel/ProductInfoPanel";
import {BuyWithList} from "../BuyWithList/BuyWithList";

// Mocks
import {CatalogItemProps} from "../CatalogItem/CatalogItem";


import {allProducts} from "../../mock/ourProducts";
import {buyWithList} from "../../mock/buyWithList";

export type ProductProps = {};

export const Product: FC<ProductProps> = () => {
    const [productData, setProductData] = useState<CatalogItemProps | undefined>(null);

    useEffect(() => {
        // @ts-ignore
        // const productId = props.match.params.id;
        const findProduct = allProducts.find((item) => item.ID === `1A`);
        setProductData(findProduct);
    }, []);

    return (
        <div className="product" >
            <Row gutter={40}>
                {
                    productData && <Col xs={24} md={12}>
                        <ImageSlider items={productData.IMAGES}/>
                    </Col>
                }

                <Col xs={24} md={10}>
                    <ProductInfoPanel {...productData} />
                </Col>
                <Col xs={24} style={{paddingTop: `40px`}}>
                    <BuyWithList items={buyWithList}/>
                </Col>
            </Row>
        </div>
    );
}